<template>
  <div class="selectBox">
    <div class="selectNum" v-if="leftSignTime.length!==0">
      <NumCard v-for="item in leftSignTime" :signTime="item" :key="item.id"/>
    </div>
    <div class="num_null" v-else>
      <img src="../../../assets/appointment/number_null.png" alt="">
      <span class="text">今天的号源已经没有了哦~</span>
      <span class="choiceOtherDate">请选择其他日期</span>
    </div>
  </div>
</template>

<script>
import NumCard from './numCard'
import store from '@/vuex/index'
import {Dialog} from 'vant'

export default {
  name: 'selectRegNum',
  components: {
    NumCard
  },
  data () {
    return {}
  },
  computed: {
    leftSignTime () {
      return store.state.wmodule.curLeftSignTime
    }
  },
  methods: {},
  created () {
    console.log(store.state.wmodule);
    const {doctScheduleId} = this.$route.params
    console.log(store.state.wmodule.curHospitalId)
    if (store.state.wmodule.curHospitalId === '') {
      Dialog.alert({
        title: '提示',
        message: '<div style="text-align:center">未选择医院和医生，请返回选择医院界面</div>'
      }).then(() => {
        this.$router.push({name: 'selectHospital'})
      })
    } else {
      store.dispatch('wmodule/getLeftSignTime', {
        doctScheduleId: doctScheduleId,
      })
    }
  }
}
</script>

<style scoped lang="less">
  .selectNum {
    width: 100%;
    box-sizing: border-box;
    padding: 0.15*2rem 0.14*2rem 0.49*2rem 0.15*2rem;
    overflow: hidden;
    text-align: center;
  }

  .num_null {
    text-align: center;

    img {
      display: block;
      margin-top: 1.18*2rem;
      width: 3.09*2rem;
      height: 1.69*2rem;
    }

    span {
      display: block;

      &.text {
        margin-top: .32*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        font-size: .14*2rem;
        color: #272727;
      }

      &.choiceOtherDate {
        margin-top: .06*2rem;
        font-size: .12*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        opacity: .8;
      }
    }
  }

  .dialogText {
    font-size: .16*2rem;
    text-align: center;
  }
</style>



