<template>
  <div
    class="numCard"
    :class="{noLeftNum:signTime.availableLeftNum==0}"
    @click="()=>{jumpFn(signTime.availableLeftNum,1)}"
  >
    <div class="num">{{signTime.startTime}}-{{signTime.endTime}}</div>
    <div class="time">
      余
      <span class="numTime">{{signTime.availableLeftNum}}</span>个
    </div>
    <div class="line"></div>
  </div>
</template>

<script>
import store from "@/vuex/index";
import { Dialog } from "vant";
export default {
  name: "numCard",
  props: ["signTime"],
  methods: {
    jumpFn(leftNum, id) {
      if (leftNum != 0) {
        // appointDate 预约日期
        // appointWorkDay 预约周几
        const { appointDate, appointWorkDay } = this.$route.params;
        this.$router.push({ name: "ackDetails", params: { cardId: id } });

        // 点击选择号码时，修改Vuex中存储的预约信息
        store.commit("wmodule/changeAppointmentDate", {
          appointDate: appointDate,
          appointWorkDay: appointWorkDay,
          signStartTime: this.signTime.startTime,
          signEndTime: this.signTime.endTime,
          signCode: this.signTime.signCode,
          id: this.signTime.id,
          doctScheduleId: this.signTime.doctScheduleId
        });
      } else {
        Dialog.alert({
          title: "提示",
          message: "<div style='text-align:center;'>当前时间没有号源，请选择其他时间</div>"
        }).then(() => {
          // on close
          return
        });
      }
      console.log(store.state.wmodule);
    }
  }
};
</script>

<style scoped lang="less">
.numCard {
  float: left;
  width: 6.9rem;
  height: 0.88rem;
  background: #fff;
  border-radius: 0.08rem;
  box-sizing: border-box;
  // padding-right: 0.44rem;
  -webkit-box-shadow: 0.16rem 0.16rem 0.5rem 0.1rem rgba(108, 181, 249, 0.1);
  box-shadow: 0.16rem 0.16rem 0.5rem 0.1rem rgba(108, 181, 249, 0.1);
  margin: 0 auto;
  position: relative;
  background: #ffffff;
  box-shadow: 3px 3px 10px 0 rgba(51, 145, 232, 0.15);
  border-radius: 2px;
  margin-bottom: 0.28rem;
  background: url("../../../assets/appointment_icon_time@2x.png") no-repeat
    0.64rem center;
  background-size: 0.32rem 0.32rem;
  .line {
    position: absolute;
    left: 0;
    top: 0;
    width: 0.24rem;
    height: 0.88rem;
    background: #3391e8;
    border-radius: 0.02rem 0 0 0.02rem;
  }
  .num {
    position: absolute;
    font-family: PingFangSC-Regular, sans-serif;
    font-size: 0.36rem;
    line-height: normal;
    padding: 0.19rem 0;
    left: 1.18rem;
    top: 0;
  }

  .time {
    position: absolute;
    font-family: PingFangSC-Light, sans-serif;
    font-size: 0.34rem;
    color: #272727;
    right: 0.44rem;
    top: 0;
    line-height: normal;
    padding: 0.19rem 0;
    letter-spacing: 0.04rem;
    .numTime {
      font-family: PingFangSC-Regular;
      font-size: 0.36rem;
      color: #ff941f;
    }
  }
  &:nth-of-type(2n) {
    margin-right: 0;
  }
  &.noLeftNum {
    .line {
      opacity: 0.45;
    }
    .time {
      color: #b1afaf;
    }
    .num {
      color: #b1afaf;
    }
  }
}
</style>



